import { Routes } from "@angular/router";
import { RouterModule } from "@angular/router";
import { NgModule } from "@angular/core";
// documentos
import { RegistroNuevoFormularioComponent } from "./documentos/registroNuevoFormulario/registroNuevoFormulario.component";
//layouts

import { PrincipalformularioLayoutComponent } from "src/app/layouts/principalformulario/principalformulario-layout.component";
//Pantallas
import { PrincipalformularioComponent } from "./dashboard/principalformulario/principalformulario.component";
import { PrincipalusuarioLayoutComponent } from "src/app/layouts/principalusuario/principalusuario-layout.component";
import { PrincipalusuarioComponent } from "./dashboard/principalusuario/principalusuario.component";
const routes: Routes = [
  {
    path: "",
    component: PrincipalformularioLayoutComponent,
    children: [
      // <%IniCatalogoR%>
      {
        path: "registroNuevoFormulario/:id",
        data: {
          title: "Formulario Unisofom",
          urls: [
            { title: "Dashboard", url: "/pages" },
            {
              title: "Formulario Unisofom",
              url: "/pages/registroNuevoFormulario",
            },
            { title: "Agregar / Editar" },
          ],
        },
        component: RegistroNuevoFormularioComponent,
      },

      // <%FinCatalogoR%>
    ],
  },

  //Pantallas
  {
    path: "principalformulario",
    component: PrincipalformularioLayoutComponent,
    children: [
      {
        path: "",
        data: {
          title: "Pantalla Principal",
          urls: [
            { title: "Pantalla Principal", url: "/pages" },
            { title: "Pantalla Principal" },
          ],
        },
        component: PrincipalformularioComponent,
      },
    ],
  },
  {
    path: "principalformulario/:id",
    component: PrincipalformularioLayoutComponent,
    children: [
      {
        path: "",
        data: {
          title: "Pantalla Principal",
          urls: [
            { title: "Pantalla Principal", url: "/pages" },
            { title: "Pantalla Principal" },
          ],
        },
        component: PrincipalformularioComponent,
      },
    ],
  },

  {
    path: "principalusuario",
    component: PrincipalusuarioLayoutComponent,
    children: [
      {
        path: "",
        data: {
          title: "Pantalla Principal",
          urls: [
            { title: "Pantalla Principal", url: "/pages" },
            { title: "Pantalla Principal" },
          ],
        },
        component: PrincipalformularioComponent,
      },
    ],
  },
  {
    path: "principalusuario/:id",
    component: PrincipalusuarioLayoutComponent,
    children: [
      {
        path: "",
        data: {
          title: "Pantalla Principal",
          urls: [
            { title: "Pantalla Principal", url: "/pages" },
            { title: "Pantalla Principal" },
          ],
        },
        component: PrincipalusuarioComponent,
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PrincipalunisofomRoutingModule {}
