//Documentos
@Component({selector: 'app-tableroRegistros', 
  templateUrl: './tableroRegistros.component.html',  
  styleUrls: ["./tableroRegistros.component.scss"],})
export class TableroRegistrosComponent implements OnInit,OnDestroy{

  private subscription: Subscription;

  // public catalogosCodigoPostal: CatalogoCodigoPostal[] = [];

  //**modal */
  entries: number = 5;
  selected: any[] = [];
  temp = [];
  activeRow: any;
  dataSource:any=[];

  public closeResult = "";
  public formularioRegistro = "modal";

  get usuarioRol(): string {
    return localStorage.getItem("usuarioRol") || "";
  }

  constructor(
    // public documentosService:DocumentosService,
    public activeModal: NgbActiveModal,
    private modalService: NgbModal,
    private router: Router,
    private registroNuevoFormularioService: RegistroNuevoFormularioService,

     //<%DocumentoRegistro%>private <%PLMi-claveDocumentoRegistro%>Service:<%PLMa-claveDocumentoRegistro%>Service
  ) {
    this.registroNuevoFormularioService.filtro='filtraPorUsuarioActivo';
  }
  ngOnInit(): void {
    this.cargarRegistroNuevoFormularios();
  }

  entriesChange($event) {
    this.entries = $event.target.value;
  }
  filterTable($event) {
    let val = $event.target.value;

    this.temp = this.dataSource.filter(function(d) {
      for (var key in d) {
        let cadena:string=d[key].toString();
        
        if (cadena.toLowerCase().indexOf(val) !== -1) {
          return true;
        }
      }
      return false;
    });
  }
  onSelect({ selected }) {
    this.selected.splice(0, this.selected.length);
    this.selected.push(...selected);
  }
  onActivate(event) {
    this.activeRow = event.row;
  }

  

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
      console.log('Suscripción cancelada');
    }


  }


  openInNewTab(route: string) {
    // Serializamos la ruta para obtener la URL completa
    const url = this.router.serializeUrl(this.router.createUrlTree([route]));
    this.router.navigateByUrl(url);
    // window.open('#'+url, '_blank'); // Abrimos la URL en una nueva pestaña
  }

  cargarRegistroNuevoFormularios() {
    let filtro=false;



    this.subscription=this.registroNuevoFormularioService.cargarRegistroNuevoFormulariosRl(filtro)
      .subscribe(registroNuevoFormularios => {
        this.dataSource = registroNuevoFormularios;
        this.temp = this.dataSource.map((prop, key) => {
          return {
            ...prop,
            id: key
          };
        });
      });
  }


  seleccionar(row) {
    this.activeModal.close(row)
    //this.registroNuevoFormularioService.registroNuevoFormularioSeleccionado = row;
    //this.activeModal.close(TableroRegistrosComponent);
  }

  buscar(termino: string) {
    // this.cargarCatalogoColeccion("colUsoCFDI", termino).subscribe(
    //   (catalogo) => {
    //     // this.catalogosCodigoPostal = catalogo;
    //     this.dataSource = new MatTableDataSource(catalogo);
    //     this.dataSource.paginator = this.paginator;
    //     this.dataSource.sort = this.sort;
    //   }
    // );
  }
  //Acciones 
  
  borrarRegistroNuevoFormulario( registroNuevoFormulario: RegistroNuevoFormulario ) {

    Swal.fire({
      title: '¿Borrar registro seleccionado?',
      text: `Esta a punto de borrar a ${ registroNuevoFormulario._id }`,
      icon: 'question',
      showCancelButton: true,
      confirmButtonText: 'Si, borrarlo'
    }).then((result) => {
      if (result.value) {
        
        this.registroNuevoFormularioService.borrarRegistroNuevoFormulario( registroNuevoFormulario._id,
           )
          .subscribe( resp => {
            
            this.cargarRegistroNuevoFormularios();
            Swal.fire(
              'Registro seleccionado borrado',
              `${ registroNuevoFormulario._id } fue eliminado correctamente`,
              'success'
            );
            
          });

      }
    })

  }


    addRegistroNuevo(): void {
    this.AbrirModal(RegistroNuevoFormularioComponent, undefined, undefined);
  }

  EditarModal(row){
    localStorage.setItem("registroNuevoFormulariostatus", 'SINFIRMAR');
    this.AbrirModal(RegistroNuevoFormularioComponent,row,undefined);
 }


AbrirModal(componente: any,row,relacion) {
  const modalRef: NgbModalRef= this.modalService
    .open(componente, {
      backdropClass: "light-blue-backdrop",
      backdrop: false,
      animation: true,
      size: "xl",
    });

    modalRef.componentInstance.registroNuevoFormulario = row;
    // modalRef.componentInstance.registroNuevoFormularioId = row?row._id:undefined;
    modalRef.componentInstance.Id = row?row._id:undefined;
    modalRef.result.then(
      (result) => {
        this.cargarRegistroNuevoFormularios();
      },
      (reason) => {
        this.cargarRegistroNuevoFormularios();
      }
    );
}

abrirDocumento(row: any): void {

  localStorage.setItem("registroNuevoFormulariostatus", 'FIRMADO');
  this.AbrirModal(RegistroNuevoFormularioComponent,row,undefined);
}

getVisible(row: any, status: string,rolAparece:string) {
  return row == status && rolAparece.includes(this.usuarioRol);
}



}

